.logo__container{
  display: flex;
  gap: 5px;
  align-items: center;
  width: 100%;
  padding: 0 20px;
  color: #B4CD93;
  h3{
    display: inline-block;
    background: linear-gradient(99.32deg, #B4CD93 7.91%, #427A5B 88.96%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
  }
}